<template>
  <v-container fluid class="fill-height hero">
    <v-row>
      <v-col class="d-flex justify-center">
        <div>
          <v-img
            class="mx-auto my-6"
            max-width="228"
            src="../assets/fitoagro_logo.png"
          ></v-img>
          <v-card class="pt-12" elevation="8" width="450" rounded="lg">
            <!-- <v-card-title class="text-h5">
              {{ steps[step].title }}</v-card-title
            > -->
            <v-window v-model="step" class="px-8">
              <v-window-item>
                <v-card-text>
                  <v-form v-model="steps[step].formValid">
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Tipo de Conta*
                    </span>
                    <v-select
                      v-model="roleSelected"
                      :items="roles"
                      item-title="agenttype_name"
                      return-object
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      prepend-inner-icon="mdi-account-outline"
                      placeholder="Selecione o tipo de conta"
                      :rules="rules.role"
                      :disabled="isLoadingRoles"
                      :loading="isLoadingOrgs"
                    ></v-select>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Organização*
                    </span>
                    <v-autocomplete
                      v-model="orgSelected"
                      :items="organizations"
                      item-title="organization_name"
                      return-object
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      prepend-inner-icon="mdi-domain"
                      placeholder="Selecione a organização"
                      :rules="rules.org"
                      :disabled="isLoadingOrgs"
                    ></v-autocomplete>
                    <!-- <v-card
                      class="mb-12"
                      color="surface-variant"
                      variant="tonal"
                    >
                      <v-card-text class="text-medium-emphasis text-caption">
                        Pode selecionar uma organização se for um membro de uma
                        organização. <b>Poderá faze-lo mais tarde.</b>
                      </v-card-text>
                    </v-card> -->
                  </v-form>
                </v-card-text>
              </v-window-item>
              <v-window-item>
                <v-card-text>
                  <v-form v-model="steps[step].formValid">
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Nome Pessoal*
                    </span>
                    <v-text-field
                      class="ma-1"
                      v-model.trim="name"
                      :rules="rules.name"
                      color="secondary"
                      density="compact"
                      placeholder="Insira o nome"
                      prepend-inner-icon="mdi-card-account-details-outline"
                      variant="outlined"
                    ></v-text-field>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Telemóvel*
                    </span>
                    <v-text-field
                      class="ma-1"
                      v-model.trim="phone"
                      :rules="rules.phone"
                      prefix="+351"
                      counter="9"
                      color="secondary"
                      density="compact"
                      placeholder="Insira o seu contacto"
                      prepend-inner-icon="mdi-phone-outline"
                      variant="outlined"
                    ></v-text-field>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Gmail*
                    </span>
                    <v-text-field
                      class="ma-1"
                      v-model.trim="email"
                      :rules="rules.email"
                      color="secondary"
                      density="compact"
                      placeholder="Insira o Gmail"
                      prepend-inner-icon="mdi-gmail"
                      variant="outlined"
                    ></v-text-field>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Cidade*
                    </span>
                    <v-text-field
                      class="ma-1"
                      v-model.trim="city"
                      :rules="rules.city"
                      color="secondary"
                      density="compact"
                      placeholder="Insira a cidade"
                      prepend-inner-icon="mdi-city-variant-outline"
                      variant="outlined"
                    ></v-text-field>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Morada*
                    </span>
                    <v-text-field
                      class="ma-1"
                      v-model.trim="address"
                      :rules="rules.address"
                      color="secondary"
                      density="compact"
                      placeholder="Insira a morada"
                      prepend-inner-icon="mdi-map-marker-outline"
                      variant="outlined"
                    ></v-text-field>
                  </v-form>
                </v-card-text>
              </v-window-item>
              <v-window-item>
                <v-card-text>
                  <v-form v-model="steps[step].formValid">
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Nome de Utilizador*
                    </span>
                    <v-text-field
                      class="ma-1"
                      v-model.trim="username"
                      :rules="rules.username"
                      color="secondary"
                      density="compact"
                      placeholder="Insira o nome de utilizador"
                      prepend-inner-icon="mdi-account-outline"
                      variant="outlined"
                    ></v-text-field>

                    <span class="text-subtitle-1 text-medium-emphasis">
                      Senha*
                    </span>
                    <v-text-field
                      class="ma-1 mb-2"
                      v-model.trim="password"
                      :rules="rules.password"
                      :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                      :type="visible ? 'text' : 'password'"
                      color="secondary"
                      density="compact"
                      placeholder="Insira a senha"
                      prepend-inner-icon="mdi-lock-outline"
                      variant="outlined"
                      @click:append-inner="visible = !visible"
                    ></v-text-field>

                    <span class="text-subtitle-1 text-medium-emphasis">
                      Confirmar Senha*
                    </span>
                    <v-text-field
                      class="ma-1 mb-2"
                      v-model.trim="confpassword"
                      :rules="rules.confpassword"
                      :append-inner-icon="
                        confirmVisible ? 'mdi-eye-off' : 'mdi-eye'
                      "
                      :type="confirmVisible ? 'text' : 'password'"
                      color="secondary"
                      density="compact"
                      placeholder="Confirme a senha"
                      prepend-inner-icon="mdi-lock-outline"
                      variant="outlined"
                      @click:append-inner="confirmVisible = !confirmVisible"
                    ></v-text-field>
                  </v-form>
                </v-card-text>
              </v-window-item>
            </v-window>
            <v-row class="mb-2">
              <v-col align="center"
                ><a
                  class="text-secondary text-decoration-none"
                  href="#/login"
                  rel="noopener noreferrer"
                  target="_parent"
                >
                  Já tem conta <v-icon icon="mdi-chevron-right"></v-icon> </a
              ></v-col>
            </v-row>
            <v-divider></v-divider>
            <v-card-actions>
              <v-btn
                v-if="step > 0"
                prepend-icon="mdi-chevron-left"
                variant="text"
                @click="step--"
              >
                Anterior
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn
                v-if="step < 2"
                :disabled="!steps[step].formValid"
                append-icon="mdi-chevron-right"
                color="secondary"
                variant="flat"
                @click="step++"
              >
                Próximo
              </v-btn>
              <v-btn
                v-else-if="step === 2"
                :disabled="!steps[step].formValid"
                color="blue"
                variant="flat"
                :loading="isLoadingPost"
                @click="postAgent()"
              >
                Adicionar
              </v-btn>
            </v-card-actions>
          </v-card>
        </div>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog" width="auto" @afterLeave="resetDialog()">
      <v-card width="500">
        <v-card-title
          class="bg-warning d-flex justify-space-between align-center"
        >
          <span>Ativação de conta</span>
          <v-btn variant="text" icon="mdi-close" @click="dialog = false"></v-btn
        ></v-card-title>
        <v-card-text>
          <span v-html="dialogMessage"></span>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { useGeneralStore } from "../stores/generalStore";

import axios from "axios";

export default {
  name: "Register",
  setup() {
    const generalStore = useGeneralStore();
    return { generalStore };
  },
  data() {
    return {
      step: 0,
      steps: [
        {
          title: "Tipo de Conta e Organização",
          formValid: false,
        },
        {
          title: "Dados Pessoais",
          formValid: false,
        },
        {
          title: "Dados de Conta",
          formValid: false,
        },
      ],
      isLoadingPost: false,
      formValid: false,
      visible: false,
      confirmVisible: false,
      name: null,
      phone: null,
      email: null,
      address: null,
      city: null,
      username: null,
      password: null,
      confpassword: null,
      rules: {
        role: [(v) => !!v || "Selecione o tipo de conta"],
        org: [(v) => !!v || "Selecione a organização"],
        name: [
          (v) => !!v || "Insira o nome",
          (v) => v.length <= 20 || "Nome muito longo",
        ],
        phone: [
          (v) => !!v || "Insira o telefone",
          (v) => v.length === 9 || "Insira um número válido",
        ],
        email: [
          (v) => !!v || "Insira o gmail",
          (v) => /.+@.+\..+/.test(v) || "Insira um gmail válido",
        ],
        address: [
          (v) => !!v || "Insira a morada",
          (v) => v.length <= 50 || "Morada muito longa",
        ],
        city: [
          (v) => !!v || "Insira a cidade",
          (v) => v.length <= 20 || "Cidade muito longa",
        ],
        username: [
          (v) => !!v || "Insira o nome de utilizador",
          (v) => v.length <= 20 || "Nome de utilizador muito longo",
        ],
        password: [
          (v) => !!v || "Insira a senha",
          (v) => v.length <= 20 || "Senha muito longa",
        ],
        confpassword: [
          (v) => !!v || "Confirme a senha",
          (v) => v === this.password || "As senhas não coincidem",
        ],
      },

      roles: [],
      roleSelected: null,
      isLoadingRoles: false,

      organizations: [],
      orgSelected: null,
      isLoadingOrgs: false,

      dialog: false,
      dialogMessage: "",
    };
  },
  computed: {
    roleName() {
      return this.roleSelected?.agenttype_name;
    },
    lasStep() {
      return this.steps.length - 1;
    },
  },
  watch: {
    roleSelected() {
      this.reset();
    },
  },
  methods: {
    getRoles() {
      this.isLoadingRoles = true;
      axios
        .get("api/agenttypes/roles")
        .then((response) => {
          this.roles = response.data;
          this.isLoadingRoles = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getOrganizations() {
      this.isLoadingOrgs = true;
      axios
        .get("api/organizations")
        .then((response) => {
          this.organizations = response.data.organizations;
          this.isLoadingOrgs = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    postAgent() {
      this.isLoadingPost = true;
      let payload = {
        agenttype_id: this.roleSelected.agenttype_id,
        organization_id: this.orgSelected?.organization_id,
        membertype_id: this.memberTypeSelected?.membertype_id,
        entity_name: this.name.trim(),
        entity_phone: this.phone.trim(),
        entity_email: this.email.trim(),
        entity_address: this.address.trim(),
        entity_city: this.city.trim(),
        agent_username: this.username.trim(),
        agent_password: this.password.trim(),
        agent_properties: null,
      };
      console.log(payload);
      axios
        .post("/api/agents", payload)
        .then((response) => {
          console.log(response);
          if (response.status === 201) {
            this.isLoadingPost = false;
            // apenas agentes de campo e produtores recebem email de ativação //HARD CODED
            console.log(response.agent);
            if (
              response.data.agent.agenttype_id == 1 ||
              response.data.agent.agenttype_id == 3
            ) {
              this.openDialog(
                "<span>Verifique o gmail para ativar a sua conta!</span>"
              );
            } else {
              this.openDialog(
                "<span>Conta criada com sucesso!</span></br><span>Aguarde que um Administrador ative a sua conta.</span>"
              );
            }
          }
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingPost = false;
          this.generalStore.errorSnackbar(error.response.data.error);
        });
    },
    openDialog(message) {
      this.dialog = true;
      this.dialogMessage = message;
    },
    resetDialog() {
      this.dialogMessage = "";
      this.$router.push("/login");
    },
    reset() {
      this.name = null;
      this.phone = null;
      this.email = null;
      this.address = null;
      this.city = null;
      this.username = null;
      this.password = null;
      this.confpassword = null;
      this.formValid = false;
      this.visible = false;
      this.confirmVisible = false;

      this.orgSelected = null;
      // this.memberTypeSelected = null;
    },
  },
  mounted() {
    this.getRoles();
    this.getOrganizations();
  },
  unmounted() {
    this.reset();
  },
};
</script>

<style scoped>
.hero {
  background: url("../assets/crops_bg.jpg") no-repeat center center;
  background-size: cover;
}
</style>
